.Loader__Center--1R-x4{
	text-align: center;
	
}

.Loader__Loader--2f_1D {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.Loader__Loader--2f_1D div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: Loader__lds-ripple---Lu5S 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: Loader__lds-ripple---Lu5S 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.Loader__Loader--2f_1D div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
@-webkit-keyframes Loader__lds-ripple---Lu5S {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
@keyframes Loader__lds-ripple---Lu5S {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
