.QuizCreator__QuizCreator--38hjy{
	display: flex;
	justify-content: center;
	padding-top: 20px;
	flex-grow: 1;
	/*height: 100vh;*/
	width: 100%;
	background: -webkit-linear-gradient(left, #b6e02c 0%, #82f2c7 100%);
	background: linear-gradient(90deg, #b6e02c 0%, #82f2c7 100%);
}
.QuizCreator__QuizCreator--38hjy > div{
	width: 100%;
